import mixin from "~/utils/mixins";
import LayoutTb from "~/components/Layout/LayoutTb";
import MyInputButton from "~/components/search/MyInputButton";
import MyPagination from "~/components/MyPagination";
import DialogNormal from "~/components/utils/dialog/DialogNormal";
import MyNormalDialog from "~/components/utils/dialog/MyNormalDialog";
import DrawerRight from "~/components/utils/dialog/DrawerRight";
import {common} from "~/utils/api/url";
import {setChildren, MessageSuccess, MessageError, MessageWarning} from "~/utils/utils";
import DrawerLayoutRight from "~/components/utils/dialog/DrawerLayoutRight";
import appFormSetValidater from "~/utils/validater/appFormSetValidater";
import MyCascader from "~/components/utils/select/MyCascader";
import PcStaticTemplate from "~/components/form/PCStaticTemplate";
import MySelect from "~/components/MySelect";
import formStaticValidater from "~/utils/validater/formStaticValidater";
import PcStaticTemplateShow from "~/components/form/PCStaticTemplateShow";

export default {
  mixins: [mixin, formStaticValidater],
  components: {
    PcStaticTemplateShow,
    MySelect,
    PcStaticTemplate,
    MyCascader,
    DrawerLayoutRight, LayoutTb,MyInputButton,MyPagination,DialogNormal,MyNormalDialog,DrawerRight},
  data(){
    return {
      drawerAddInfo: false,
      drawerVisible: false,
      drawerLoading: false,
      visibleConfim: false,
      dialogLoading: false,
      btnLoading: false,
      refreshTeacherStatus: false,
      refreshStudentStatus: false,
      tableData: [],
      searchKey: '',
      departmentPath: '',
      teacherArray: [],
      approverUsers: [],
      roleList: [],
      errorTeacher: '',
      roleOptions: [],
      title: '',
      titleType: '',
      searchCommDeptData: [],
      staticFormList: [],
      searchStaticForm: '',
      staticPcFormList: [],
      staticPcFormData: [],
      formId: '',
      staticId: '',
      typeList: [
        {"label": 'PC端模板', value: true},
        // {"label": 'H5端模板', value: false}
      ],
      form: {
        id: '',
        name: '',
        des: '',
        roleType: 0,
        roleOprLook: false,
        roleOprDel: false,
        roleOprSubmit: false,
        roleOprImOrEx: false,
        roleTeachers: false,
        roleStudents: false,
        roleUsers: [],
        roleUserGroup: [],
        enable: true,
        permissionContent: '',
        permissionContentArray: [],
        approverTeacherId: [],
        approverStudentId: [],
        roleList: []
      },
    }
  },
  created() {
    this.init();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num,
        searchKey: this.searchKey,
        departmentPath: this.departmentPath
      };
      this.$axios.get(common.static_appinfo_form_page, {params: params}).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    search(data){
      this.searchKey = data.input;
      this.page = 1;
      this.init();
    },
    handleCascaderChange(data){
      this.departmentPath = data[data.length-1];
      this.page = 1;
      this.init();
    },
    openedForm(){
      if(this.$refs['pcRef']){
        this.$refs['pcRef'].initAsync();
      }
    },
    settingInfo(data){
      this.staticId = data.id+"";
      this.drawerVisible = true;
    },
    closeDrawerDialog(){
      if(this.$refs['pcRef']){
        this.$refs['pcRef'].closeDialog();
      }
      this.drawerVisible = false;
    }
  }
}
